import { Component, OnInit } from '@angular/core';
import { MicrostrategyService } from '../../@core/service/microstrategy.service';
import { LocalService } from '../../@core/service/local.service';
import { DataStoreService } from '../../@core/service/data-store.service';
import { RBACINFO } from '../../@core/urls/analytics-url.config';
import { Subscription } from 'rxjs';

@Component({
  selector: 'lib-analytics',
  templateUrl: './analytics.component.html',
  styleUrls: ['./analytics.component.scss']
})
export class AnalyticsComponent implements OnInit {
  environment: any;
  RBACORG: RBACINFO = new RBACINFO();
  orgSubs!: Subscription;
  orgId: any;
  constructor(private mstrService: MicrostrategyService, private localstore: LocalService,private _storeservice: DataStoreService) {
    this.orgSubs =  this._storeservice.currentStore.subscribe((res: any) => {
      if (res['RBACORG'] && res['RBACORG'] !== '') {
        this.RBACORG = res['RBACORG'];
        console.log(this.RBACORG, 'RBACORG Profile');
        this.environment = this.RBACORG['environment'];
        this.orgId = parseInt(this.RBACORG['orgID']);
        if(this.environment){
          this.getDossier();
        }
      }
    });
  }

  ngOnInit(): void {


  }

  async getDossier() {
    const user = this.localstore.getObj('user');
    if ('user') {
      const dossierConfig = user && JSON.parse(user.userroles[0]?.dossierid);
      const projectId = dossierConfig?.projectId;
      const dossierId = dossierConfig?.id;
      const pageNo = '';
      this.mstrService.getDossier(projectId, dossierId, pageNo);
    }
  }
  ngAfterContentInit() {
    setTimeout(() => {
      this.getDossier();
    }, 100);
  }
  ngOnDestroy(): void {
    this.orgSubs.unsubscribe();
  }
}
