import { Component, Input, OnInit } from '@angular/core';
import { RBACINFO } from './@core/urls/analytics-url.config';
import { Observable } from 'rxjs';
import { PermissionStore } from './@core/permissions/permission.store';
import { DataStoreService } from './@core/service/data-store.service';

@Component({
  selector: 'microstrategy',
  template: `
  <lib-analytics></lib-analytics>

  `,
  styles: [
  ]
})
export class MicrostrategyComponent implements OnInit {
  @Input() public RBACORG?: RBACINFO = new RBACINFO();
  @Input() public PERMISSION?: any;
  @Input() public analyticsEvent!: Observable<any>;

  constructor(private permissionStore: PermissionStore,
    private _storeservice: DataStoreService) { }

  ngOnInit() {
    this.analyticsEvent.subscribe((val: any) => {
     this.RBACORG = val.RBACORG;
     this.PERMISSION = val.PERMISSION;
     this._storeservice.setData('RBACORG', this.RBACORG);
     this.permissionStore.setStore(this.PERMISSION);
   })
 }

}
